import * as React from 'react';
import Config from "../config";
import FileDropper from "./FileDropper";
import Box from "@mui/material/Box";
import Grid from '@mui/material/Grid';
import TextField from "./TextField";

type InputAreaProps = {
    config?: Config;
    data: FormData;
};

function getFileDropper(props: InputAreaProps, item: { name: string; type: string, [key: string]: any }) {
    const contentTypes:{[key: string]: string[]} = {}
    const contentTypeList : string[] = item.contentTypes
    if(item.contentTypes){
        for(const type of contentTypeList){
            contentTypes[type] = []
        }
    }
    return (
        <Grid container spacing={2}>
            <FileDropper contentTypes={contentTypes} filePassed={file => {
                props.data.set(item.name, file)
            }}/>
        </Grid>
    )
}

function getTextField(props: InputAreaProps, item: { name: string; type: string,  }) {
    return (
        <Grid container spacing={2} sx={{ py: 5}}>
            <TextField
                label={item.name}
                value=""
                onChange={text => {
                    props.data.set(item.name, text)
                }}
            />
        </Grid>
    )
}

function buildComponents(props: InputAreaProps, config: Config) {
    return config.interface.input.map((item: { name: string; type: string, [key: string]: any }) => {
        switch (item.type) {
            case 'file':
                return getFileDropper(props, item)
            case 'text':
                return getTextField(props, item)
            default:
                return undefined
        }
    })
}


const InputArea: React.FC<InputAreaProps> = (props: InputAreaProps) => {
    const components = props.config ? buildComponents(props, props.config!) : [];
    return (
        <Box sx={{ flexGrow: 1 }}>
            {components}
        </Box>
    );
};

export default InputArea;
