import React, { useCallback, useMemo, useState } from 'react';
import Paper from '@mui/material/Paper';
import {styled} from '@mui/material/styles';
import {useDropzone} from 'react-dropzone';

const Item = styled(Paper)(({theme}) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

const baseStyle = {
    flex: 1,
    display: 'flex',
    flexDirection: 'column' as 'column',
    alignItems: 'center',
    padding: '20px',
    borderWidth: 2,
    borderRadius: 2,
    borderColor: '#eeeeee',
    borderStyle: 'dashed',
    backgroundColor: '#fafafa',
    color: '#bdbdbd',
    outline: 'none',
    transition: 'border .24s ease-in-out',
    height: '100%',
};

const focusedStyle = {
    borderColor: '#2196f3'
};

const acceptStyle = {
    borderColor: '#00e676'
};

const rejectStyle = {
    borderColor: '#ff1744'
};

type FileDropperProps = {
    contentTypes: {[key: string]: string[]};
    filePassed: (file: File) => void;
}

// https://react-dropzone.js.org/
export default function FileDropper(props: FileDropperProps) {
    const [file, setFile] = useState<File|undefined>(undefined)
    const onDrop = useCallback((acceptedFiles: File[]) => {
            console.log(acceptedFiles);
            const file = acceptedFiles[0];
            setFile(file)
            props.filePassed(file);
        },
        [])
    const {
        isDragActive,
        getRootProps,
        getInputProps,
        isFocused,
        isDragAccept,
        isDragReject
    } = useDropzone({onDrop, multiple: false, accept: props.contentTypes});
    const style = useMemo(() => ({
        ...baseStyle,
        ...(isFocused ? focusedStyle : {}),
        ...(isDragAccept ? acceptStyle : {}),
        ...(isDragReject ? rejectStyle : {})
    }), [
        isFocused,
        isDragAccept,
        isDragReject
    ]);
    return (
        <div {...getRootProps({className: 'dropzone', style})}>
            <input {...getInputProps()} />
            {
                isDragActive ?
                    <p>Drop the files here ...</p> :
                    file ? <Item>{file.name}</Item> :
                    <p>Drag 'n' drop some files here, or click to select files</p>
            }
        </div>
    )
}
