import React, { useState, useEffect } from 'react';
import { ThemeProvider } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';
import theme from './themes';
import { CircleSpinnerOverlay, FerrisWheelSpinner } from 'react-spinner-overlay'
import Header from './components/Header'
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import FileDropper from './components/FileDropper';
import JsonResultViewer from './components/JsonResultViewer';
import axios from 'axios';
import Config from './config';
import ApiClient from "./utilities/api_client";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'
import InputArea from "./components/InputArea";
import Button from "./components/Button"; // Import css


function App() {
    const [json, setJson] = useState(undefined)
    const [isLoading, setIsLoading] = useState(false)
    const [config, setConfig] = useState<Config>()
    const [data, setData] = useState(new FormData())
    const apiClient = new ApiClient()
    const fetchConfig = () => {
        apiClient.get<Config>('/api/config').then((response: { data: Config }) => {
            setConfig(response.data);
        }).catch((e: Error) => {
            confirmAlert({
                title: 'Failed to load config',
                message: e.message + ". Please refresh the page.",
                buttons: [
                    {
                        label: 'OK',
                        onClick: () => {
                            window.location.reload()
                        }
                    },
                ],
            })
        });
    }
    useEffect(() => {
        fetchConfig()
    }, [])
    return (
        <ThemeProvider theme={theme}>
            <CircleSpinnerOverlay
                loading={isLoading}
                overlayColor="rgba(0,153,255,0.2)"
            />
            {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
            <CssBaseline/>
            <Header config={config}/>
            <Box sx={{flexGrow: 1, padding: 5}}>
                {config ? config.description : ''}
            </Box>
            <Box sx={{flexGrow: 1, padding: 5, height: '80vh'}}>
                <Grid container spacing={10} sx={{height: '100%'}}>
                    <Grid item xs={6} sx={{height: '100%'}}>
                        <InputArea data={data} config={config}/>
                        <Button label="Send" onClick={()=>{
                            setIsLoading(true)
                            apiClient.post<FormData>('/api/entrypoint', data).then(
                                (response: { data: any; }) => {
                                    console.log(response)
                                    setIsLoading(false)
                                    setJson(response.data)
                                }
                            ).catch(function (error) {
                                setJson(undefined)
                                setIsLoading(false)
                                confirmAlert({
                                    title: 'Failed to execute model',
                                    message: error.message,
                                    buttons: [
                                        {
                                            label: 'OK',
                                            onClick: () => {
                                                window.location.reload()
                                            }
                                        },
                                    ],
                                })
                                if (error.response) {
                                    console.log(error.response.data);
                                    console.log(error.response.status);
                                    console.log(error.response.headers);
                                } else if (error.request) {
                                    console.log(error.request);
                                } else {
                                    console.log('Error', error.message);
                                }
                            })
                        }}/>
                    </Grid>
                    <Grid item xs={6} sx={{height: '100%'}}>
                        <JsonResultViewer jsonData={json}/>
                    </Grid>
                </Grid>
            </Box>
        </ThemeProvider>
    );
}

export default App;
